.profie__info {
  margin: 50px auto;
  width: 700px;
  height: 650px;
  background-color: #ffff;
  box-shadow: 0px 2px 2px #00000029;
  border-radius: 10px;
  padding: 30px;
}

.info__title {
  font-size: 20px;
  color: #7d8078;
}
.avatar {
  display: flex;
  align-items: center;
  margin-top: 20px;
}
