.change__avatar {
  border-bottom: 1px solid grey;
  padding-bottom: 5px;
  cursor: pointer;
  font-size: 16px;
  color: #7d8078;
}
.avatar img {
  border-radius: 10px;
  height: 130px;
  margin-right: 30px;
}

form {
  display: flex;
  flex-direction: column;
  margin-top: 30px;
  font-size: 18px;
  position: relative;
}

form * {
  margin-bottom: 8px;
}

form input {
  height: 35px;
  background: #f9f9f9 0% 0% no-repeat padding-box;
  border: 0.5px solid #707070ad;
  border-radius: 5px;
  width: 80%;
  padding-left: 10px;
  outline: none;
  font-size: 16px;
  color: #7d8078;
}

.save__btn {
  background: #ec5257 0% 0% no-repeat padding-box;
  border-radius: 15px;
  border-style: none;
  color: white;
  height: 50px;
  width: 130px;
  font-size: 22px;
  margin-top: 50px;
}

.profile__email__input {
  border-style: none;
  color: white;
  background-color: #ec5257;
}
.save__popup {
  background-color: #f4f2f0;
  padding: 10px;
  width: 250px;
  border-radius: 10px;
  position: absolute;
  bottom: 60px;
  animation: fadeOut 3s forwards;
  animation-delay: 1s;
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
.user__name {
  font-size: 18px;
  margin-top: 20px;
}

.user__name span {
  font-weight: 500;
  font-size: 20px;
}
